.container{
  margin-bottom: 23px;
  height: 80px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  position: relative;
}

.corner{
  width: 0;
  height: 0;
  position: absolute;
  top: -65px;
  left: -90px;
  border-bottom: solid 50px transparent;
  border-top: solid 90px white;
  border-left: solid 90px transparent;
  border-right: solid 90px transparent;
  transform: rotate(135deg);
}
.basic{
  margin-bottom: -10px;
}

.info{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info h1{
  font-family: sans-serif;
  font-weight: bold;
  height: fit-content;
  display: inline-block;
  font-size: 1.5rem;
  margin: 15px auto -5px;
  padding-left: 40px;
}

.info p{
  text-align: center;
  height: fit-content;
  background-color: lightgrey;
  border-radius: 30px;
  padding: 2px;
  box-shadow: 0 0 3px 2px rgb(80, 80, 80);
  font-size: .75rem;
  margin-left: -15px;
  width: 110%;

  }
    .hp{
      align-self: center;
      margin-right: 10px;
    }