
.container{
  width: fit-content;

}
.dropdownMenu{
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  background-color: white;
  margin-top: 5px;
  padding-left: 10px;
  padding-top: 10px;
  height: 400px;
  overflow-y: hidden;
  box-shadow: inset 0 0 10px 3px black;
  font-family: sans-serif;
  font-weight: 500;
  
}
.button{
  cursor: pointer;
  width: 50px;
  border-radius: 0 15px 15px 0;
  border-left: none;
}

.input{
  padding: 0 10px 0 0;
  border: 3px solid rgb(75, 127, 232);
  margin-top: 10px;
  font-family: sans-serif;
  border-radius: 15px;
  background-color: white;
  display: flex;
}

.dropdown{
  border-radius: 15px 0 0 15px;
  padding-left: 15px;
  width: 310px;
  height: 50px;
  border-right: none;
}

.clear{
  width: 25px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 15px;
  height: 50px;
  border-left: none;
  border-right: none;
}