
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 15px;
  background-color: rgb(237, 237, 237);
  border: 1px solid black;
  height: 700px;
  width: 720px;
  border-radius: 15px;
  border: 3px solid rgb(75, 127, 232);
  box-shadow: inset 0 0 10px 3px black;

}

.container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 15px;
  padding: 30px 37px 20px 37px;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  z-index: 1;
  border-radius: 15px;
  scroll-behavior: smooth;
}




