.container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: -30px 0 0 -37px;
  width: 100%;
  height: 100%;
  opacity: 1;
  position: absolute;
  z-index: 10;
}

.container::before{
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  border-radius: 15px;
  opacity: .5;
  position: absolute;
}