
.wrapper{
  width: 90%;
  margin: 0 auto 25px;
  height: 180px;
  border-radius: 5px;
  padding: 5px;
  position: relative;
}
.whapperColor{
  background-image: repeating-linear-gradient( 105deg, 
  var(--gold) 0% , 
  var(--dark-shadow) 15%,
  var(--gold) 30%);
  filter: drop-shadow(5px 15px 15px black);

}
.pBg{
  width: 90%;
  height: fit-content;
  margin: -22px auto 0 ;
  border-radius: 5px;
  padding: 2px;

}
.pBg p {
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: .5rem;
  padding: 2px;
  border-radius: 5px;
  ;
  background-color: white;
}

.bg{
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;

}

.bg::before{
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: .5;
  background-image: url("/public/img/holografic.jpg");
  background-position: center;
  background-size: cover;
}

.bg img{
  transform: scale(.70);
  margin-top: -25%;
}