.container{
  display: grid;
  grid-template-columns: auto 1fr auto;
  height: 89px;
  border-radius: 15px;
  box-shadow: inset 0 0 15px 5px white;
  border: 1px solid lightcoral;
  filter: drop-shadow(5px 10px 10px black);

}

.container img {
  height: 80px;
  width: 100%;
 
}

.info{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.name{
  display: flex;
  padding: 10px 5px 0;
  justify-content: space-between;
}



.abilities{
  font-size: .65rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5px 5px 10px;
  gap: 5px;
}

.abilities div{
  padding: 2px;
  box-shadow: 0 0 3px 1px black;
  border-radius: 5px;
}

.close{
  cursor: pointer;
  justify-self: center;
  margin: 5px 5px 0 0;
}