.container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 200px;
  width: 150px;
  border-radius: 20%;

  background-image: repeating-linear-gradient( 105deg, 
  var(--gold) 0% , 
  var(--dark-shadow) 30%,
  var(--gold) 50%);
  filter: drop-shadow(5px 15px 15px black);
  justify-content: center;
  align-items: center;
  z-index: 1;

}


.container::before{
  content: '';
  border-radius: inherit;
  top:2.5%;
  left: 2.5%;
  position: absolute;
  width: 95%;
  height: 95%;
  background-color: white;
  z-index: -3;

}

.header{
  display: flex;
  justify-content: space-between;
  width: 80%;
  
}

.name{
  justify-self: center;
  align-self: center;
  display: inline-block;
  text-align: center;
}

.className{
 /* border: 1px solid black; */
 border-radius: 10px;
 padding: 3px 5px;
 box-shadow: inset 0 0 5px black;
