
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;

}


.containerImg{
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  padding: 5px;
  background-color: lightgrey;
  border-radius: 50%;
  border: 3px solid gray;
  
}

.stage{
  margin-left: 9px;
  color: rgb(91, 91, 91);
  text-shadow: rgb(94, 92, 85) 0 0 2px;
}


.stage2{
  margin:  20px 0 25px 9px;
  color: rgb(91, 91, 91);
  text-shadow: rgb(94, 92, 85) 0 0 2px;
}


.img{
  width: 60px;
  height: 60px;
}