.container{
  display: grid;
  grid-template-rows: 50px 1fr;
  align-items: flex-start;
  background-color: rgb(237, 237, 237);
  padding: 3px;
  margin-left: 10px;
  border: 3px solid rgb(75, 127, 232);
  box-shadow: inset 0 0 10px 3px black;
  height: 700px;
  width: 250px;
  border-radius: 15px;
  gap: 4px;
}
.container p{ 
  
  text-align: center;
  font-size: 2.5rem;
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 0 0;
  background-color: rgba(0, 0, 0, 0.4);

}


.cards{
  display: flex;
  flex-direction: column;
  gap: 2px;
}