.container{
  background-color: white;
  box-sizing: content-box;
  height: 160px;
  font-size: .55rem;
  width: 85%;
  margin: 20px auto 0;
  padding: 10px;
  position: relative;
}

.container h2{
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.container span{
  font-weight: 700;
  font-size: .6rem;
}
.abilities{
  height: 60px;
  width: 100%;
  
  overflow-y: auto;
}

.basic{
  height: 128px;
}