.dropdown{

  border-radius: 15px;
  width: 400px;
  margin: 3 auto;
  position: relative;
  align-self: baseline;
}
.dropdownBtn{
  border-radius: 15px;
  font-family: sans-serif;
border: 2px solid rgb(75, 127, 232);
  user-select: none;
  padding: 10px;
  background: #fff;
  box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
  font-weight: bold;
}
.dropdownBtn{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.downButton{
  width: 2px;
  height: 2px;
  border: 5px solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: translateY(45deg);
  -webkit-transform: rotate(45deg);
  cursor: pointer;
}


.dropdownContent{
  position: absolute;
  top: 110%;
  padding: 15px;
  background: #fff;
  box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
  font-weight: 500;
  color: #333;
  border-radius: 15px;
  box-shadow: inset 0 0 10px 3px black;

  width: 100%;
  z-index: 1;
}

.dropdownItem{
  padding: 5px;
  cursor: pointer;
  transition: all 0.2s;
}

.dropdownItem:hover{
  background-color: darkcyan;
  color: white;
}

.close{
  font-family: sans-serif;
  font-size: 1.25rem;
  cursor: pointer;
  margin-left: 250px;
}

